button:focus {
    box-shadow: none;
    -webkit-box-shadow: none;
    outline: none;
}

.profile-wrapper {
    display: flex;
    flex-direction: 'row';
    justify-content: center;
    align-items: center;
    width: 100%;
    height: calc(100vh - 51px);
    background-color: white;
    overflow: hidden;
}
.profile-main-panel {
    max-width: 975px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0;
    padding-left: 10px;
    padding-right: 10px;
    flex-grow: 1;
    -webkit-transition: box-shadow .2s linear;
    -moz-transition: box-shadow .2s linear;
    -ms-transition: box-shadow .2s linear;
    -o-transition: box-shadow .2s linear;
    transition: box-shadow .2s linear;
    display: -webkit-box;
    display: -ms-flexbox;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1 1 auto;
}
.profile-left-panel {
    display: flex;
    flex-direction: column;
    width: calc(100% - 370px);
    height: calc(100vh - 51px);
    border-left: 1px solid #bbb;
    border-right: 1px solid #bbb;
    overflow-y: scroll;
}

.profile-regular-title {
    display: flex;
    width: 100%;
    height: 50px;
    background-color: #E3E3E3;
    justify-content: left;
    align-items: center;
    font-size: 14px;
    font-weight: bold;
    padding-left: 20px;
}
.profile-header-title {
    display: flex;
    width: 100%;
    height: 50px;
    background-color: #E3E3E3;
    justify-content: space-between;
    align-items: center;
    font-size: 14px;
    font-weight: bold;
    padding-left: 10px;
}
.profile-agent-title {
    display: flex;
    flex-direction: column;
    width: calc(100% - 80px);
}

.profile-contact-info {
    display: flex;
    flex-direction: column;
    padding: 0 20px 10px 20px;
}
.profile-contact-title {
    font-size: 14px;
    font-weight: bold;
}
.profile-contact-info>div {
    display: flex;
    flex-direction: column;
}
.profile-phone-button {
    border: none;
    background-color: white;
    color: #0386E1;
    font-weight: 600;
}
.profile-link-button {
    border: none;
    background-color: #E3E3E3;
    color: #0386E1;
    text-decoration: underline;
    font-weight: 600;
    font-size: 12px;
}
.profile-linking {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 20px 20px 20px 20px;
}
.profile-link-wrapper {
    display: flex;
    width: 100%;
    margin-top: 10px;
    justify-content: center;
    align-items: center;
}
.profile-share-link {
    border: none;
    background-color: #DC4646;
    padding: 1px 50px 1px 50px;
    border-radius: 5px;
    font-size: 13px;
    font-weight: bold;
    color: white;
}

.profile-item {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 20px;
    background-color: white;
    border-bottom: 1px solid #bbb;
    border-top: none;
    border-left: none;
    border-right: none;
}
.profile-item:hover {
    background-color: #eee;
}
.profile-item>div>span {
    margin-left: 20px;
}

.profile-item1 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 20px;
    background-color: white;
    border-bottom: 1px solid #bbb;
    border-top: 1px solid #bbb;
    border-left: none;
    border-right: none;
}
.profile-item1:hover {
    background-color: #eee;
}
.profile-item1>div>span {
    margin-left: 20px;
}

.profile-item3 {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 20px;
    background-color: white;
    border-bottom: 1px solid #bbb;
    border-top: none;
    border-left: none;
    border-right: none;
}

.profile-edit-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 8888;

    display: flex;
    justify-content: center;
    align-items: center;
}
.profile-edit-wrapper {
    display: block;
    flex-direction: column;
    width: 300px;
    background-color: #E3E3E3;
    border-radius: 10px;
}

.profile-edit-title {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 300px;
    padding: 20px 10px 10px 10px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border: none;
}
.profile-edit-title>span {
    font-size: 18px;
    font-weight: bold;
    color: black;
}

.profile-edit-description {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    width: 300px;
    padding: 0 10px 10px 10px;
    border: none;
}
.profile-edit-description>span {
    font-size: 13px;
    color: '#555';
    text-align: center;
}

.profile-edit-input-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* align-items: center; */
    width: 300px;
    padding: 10px 10px 30px 10px;
    border: none;
}
.profile-edit-input-container>span {
    margin-left: 38px;
}
.profile-edit-input-container>div {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 35px;
    background-color: white;
}
.profile-edit-input-container>div>div {
    display: flex;
    width: 35px;
    height: 35px;
    justify-content: center;
    align-items: center;
    background-color: white;
    border-right: 1px solid #bbb;
}
.profile-edit-input-container>div>input {
    width: 240px;
    height: 30px;
    padding: 5px;
    text-align: left;
    font-weight: 400;
    color: black;
    background-color: white;
    border: none;
    border-radius: 3px;
}
.profile-edit-input-error {
    color: red;
}

.profile-edit-buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 300px;
    height: 50px;
    border: none;
}
.profile-cancel-button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 40px;
    background: #EA4A4A;
    border-bottom-left-radius: 10px;
    border: none;
    margin-top: 10px;
}
.profile-cancel-button>span {
    font-size: 16px;
    font-weight: bold;
    color: white;
}
.profile-cancel-button:hover {
    cursor: pointer;
    background-color: #973232;
}
.profile-submit-button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 40px;
    background: #0386E1;
    border-bottom-right-radius: 10px;
    border: none;
    margin-top: 10px;
}
.profile-submit-button>span {
    font-size: 16px;
    font-weight: bold;
    color: white;
}
.profile-submit-button:hover {
    cursor: pointer;
    background-color: #3080ca;
}

.profile-disable-button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 40px;
    background: #0072DC80;
    border-bottom-right-radius: 10px;
    border: none;
    margin-top: 10px;
}
.profile-disable-button>span {
    font-size: 16px;
    font-weight: bold;
    color: white;
}

.referred-item {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 20px;
    background-color: white;
    border-bottom: 1px solid #bbb;
    border-top: none;
    border-left: none;
    border-right: none;
}
.referred-name-part {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.referred-viewing-part {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 150px;
    height: 25px;
    border: 1px solid #999;
    background-color: #eee;
    border-radius: 3px;
    padding: 5px;
}
.referred-viewing-part:hover {
    background-color: #f5f5f5;
}

